body {
    background: #222222;
    color: white;
    font-size: 16px;
    height: 100vh;
}

header {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;

    img {
        width: 80%;
        grid-column-start: 2;
        padding-top: 50px;
        padding-bottom: 50px;
    }
}

#quotes-content {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    grid-auto-rows: 100px;

    h2, p, section {
        grid-column-start: 2;
    }

    p {
        font-size: 45px;
    }

    section {
        align-items: center;
        button {
            background: #222222;
            border-color: #00cc00;
            color: #00cc00;
            padding: 5px 15px;
            font-size: 20px;
        }
    }
}

footer {
    display: grid;
    grid-template-columns: repeat(2, 1fr);

    .footer-content{
        grid-column-start: 2;
        color: white;
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        align-content: flex-end;

        span {
            color: #00cc00;
        }
    }
}